nav{
    width: 100vw;
    height: 3.5rem;
    display: grid;
    place-items: center;
    background: var(--color-primary);
    position: fixed;
    top: 0;
    left: 0;
    z-index: 9;
    box-shadow:0 1rem 1.5rem hsla(var(--primary-hue),68%,42%,20%) ;
    transition: var(--transition);
}
.nav__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.nav__logo{
    width: 2.5rem;
    border-radius: 50%;
    overflow: hidden;
}
.nav__menu{
    display: flex;
    gap: 3.5rem;
}
#theme__icon{
    background: transparent;
    color: white;
    font-size: 2.5rem;
    cursor: pointer;
}
.small{
    color: white;
}
/* media query for tablets  */
@media screen and (max-width:900px) {
    .nav__menu{
        display: none;
    }
    
}